import { createRouter, createWebHistory } from 'vue-router'
import IntroScreen from '../components/onboarding/IntroScreen.vue'
import WelcomeScreen from '../components/welcomeScreen/WelcomeScreen.vue'
import LoginForm from '../components/auth/LoginForm.vue'
import RegistrationForm from '../components/registration/RegistrationForm.vue'
import BarberShopDetail from '../components/details/BarberShopDetail.vue'
import BarberShopOwnerDetail from '../components/onboarding/owner/BarberShopOwnerDetail.vue'
import AddServicesForm from '../components/ownerServices/AddServicesForm.vue'
import ServiceSelection from '../components/bookingServices/ServiceSelection.vue'
import AppointmentDetails from '../components/AppointmentDetails/AppointmentDetails.vue'
import InvoiceComponent from "../components/bookingConfirmation/InvoiceComponent.vue";
import CustomerQueue from "../components/queue/CustomerQueue/CustomerQueue.vue";


const routes = [
  {
    path: '/',
    name: 'home',
    component: IntroScreen,
    meta: { 
      transition: 'page-opacity',

    },
    
  },
  {
    path: '/onboarding',
    name: 'onboard',
    component: WelcomeScreen,
    meta: { 
      transition: 'page-opacity',
    },
  },
  {
    path: '/owner-onboarding',
    name: 'owner-onboarding',
    component: BarberShopOwnerDetail,
  },
  {
    path: '/owner-onboarding/services-rendered',
    name: 'services-rendered',
    component: AddServicesForm,
  },
  {
    path: '/appointment',
    name: 'customer-appointment',
    component: ServiceSelection,
  },
  {
    path: '/appointment/details',
    name: 'customer-booking-details',
    component: AppointmentDetails,
  },
  {
    path: '/appointment/confirmation',
    name: 'customer-booking-confirmation',
    component: InvoiceComponent,
  },
  {
    path: '/store/queue',
    name: 'store-queue',
    component: CustomerQueue,
  },
  
  {
    path: '/login',
    name: 'login',
    component: LoginForm,
    meta: { 
      transition: 'page-opacity',
    },
  },{
    path: '/register',
    name: 'full-registration',
    component: RegistrationForm
  },{
    path: '/home',
    name: 'barber-home',
    component: BarberShopDetail,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  // history: createWebHashHistory(), 
  routes
})

export default router
