<template>
  <nav
    class="flex gap-1 items-center w-full text-sm mt-20 font-medium text-gray-500"
    aria-label="Queue status"
  >
    <div
      class="flex flex-1 shrink gap-1 items-center self-stretch p-1 my-auto w-full bg-indigo-50 rounded-2xl basis-0 min-w-[240px]"
    >
      <button
        v-for="(tab, index) in tabs"
        :key="index"
        :class="[
          'flex-1 shrink gap-2.5 self-stretch px-6 py-2 my-auto min-h-[32px] rounded-[100px]',
          { 'bg-white shadow-sm text-slate-900': tab.active },
        ]"
        :aria-current="tab.active ? 'page' : undefined"
        @click="setActiveTab(index)"
      >
        {{ tab.name }}
      </button>
    </div>
  </nav>
</template>

<script setup>
import { ref } from "vue";
const props = defineProps({
  activeQueueTab: {
    type: String,
    required: true,
  },
})

const emit = defineEmits(["update:activeQueueTab"]); 

const tabs = ref([
  { name: "In Queue", active: true },
  { name: "Ongoing", active: false },
  { name: "Finished", active: false },
]);

const setActiveTab = (index) => {
  tabs.value.forEach((tab, i) => {
    tab.active = i === index;
    if (tab.active == true){
      emit("update:activeQueueTab", tab.name);
      console.log(tab.name);
    }
    
    console.log(props.activeQueueTab);
    
  });

  
};
</script>
