<template>
  <form
    @submit.prevent="handleSubmit"
    class="flex flex-col mt-6 max-w-full text-sm min-h-[801px] w-[339px]"
    role="form"
  >
  <div v-if="errorBool" class="py-3">
        <h3 class="text-red-700">
          {{ loginErrorMsg }}
        </h3>
      </div>
    <registration-input
      v-model="formData.name"
      id="name"
      label="Name"
      type="text"
      icon="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/774ad77e50b885501a2885d18fa65f9e7393b1df7da186048ed483a9c661e303?apiKey=a8777e7eadb646868aa33e965a4c2028&"
    />
    
    <registration-input
      v-model="formData.username"
      id="username"
      label="email"
      type="email"
      icon="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/577ae1a8b92781c7327ceb4f51718a29c5b5ec5a7dd44b00768fe32a38ba7774?apiKey=a8777e7eadb646868aa33e965a4c2028&"
      class="mt-5"
    />

    <registration-select
      v-model="formData.role"
      id="role"
      label="Role Register"
      :options="roleOptions"
      class="mt-5"
    />

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="storeInfo.storeName"
      id="storeName"
      label="Store Name"
      type="text"
      placeholder="What is your store name..?"
      class="mt-5"
    />

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="storeInfo.googleMapsLink"
      id="googleMapsLink"
      label="Google Maps Link/Address"
      type="text"
      placeholder="Enter Google Maps link here..."
      class="mt-5"
    />

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="storeInfo.aboutInfo.about"
      id="about"
      label="Store About"
      type="text"
      placeholder="Tell the world about your store"
      class="mt-5"
    />

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="formData.address"
      id="address"
      label="Physical Address"
      type="text"
      placeholder="Let the world know your store location"
      class="mt-5"
    />

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="storeInfo.aboutInfo.operatingHours.weekHours.openingTime"
      id="weekOperatingHours"
      label="Open Hours during the week (Mon - Fri)"
      type="text"
      placeholder="e.g. 09:00 - 21:00"
      class="mt-5"
    />
    

    <registration-input
      v-if="formData.role == 'owner'"
      v-model="storeInfo.aboutInfo.operatingHours.weekendHours.openingTime"
      id="weekendOperatingHours"
      label="Open Hours during the weekends (Sat - Sun)"
      type="text"
      placeholder="e.g. 09:00 - 21:00"
      class="mt-5"
    />

    <registration-phone-input v-model="formData.phone" class="mt-5" />

    <registration-password-input
      v-model="formData.password"
      id="password"
      label="Create password"
      :show-password="showPassword"
      @toggle-visibility="togglePassword"
      class="mt-5"
    />

    <registration-password-input
      v-model="formData.confirmPassword"
      id="confirmPassword"
      label="Confirm password"
      :show-password="showConfirmPassword"
      @toggle-visibility="toggleConfirmPassword"
      class="mt-5"
    />

    <button
      type="submit"
      class="overflow-hidden gap-2.5 self-stretch px-2.5 py-4 mt-6 max-w-full text-base font-bold text-center text-white whitespace-nowrap rounded-lg bg-slate-700 w-[339px] hover:bg-slate-800 focus:outline-none focus:ring-2 focus:ring-slate-500"
      aria-label="Register account"
    >
      Register
    </button>
  </form>
</template>

<script setup lang="ts">
import { ref, reactive } from "vue";
import RegistrationInput from "./form/RegistrationInput.vue";
import RegistrationSelect from "./form/RegistrationSelect.vue";
import RegistrationPhoneInput from "./form/RegistrationPhoneInput.vue";
import RegistrationPasswordInput from "./form/RegistrationPasswordInput.vue";
import { register } from "@/components/api/ApiUtil";
import { useRouter } from 'vue-router'
import { useCookies } from '@vueuse/integrations/useCookies'
import { AboutInfo, OperatingHours, RegisterDto, StoreInfo, TimeRange } from "@/components/interfaces/auth/RegisterDtoInterface";
const cookies = useCookies(['locale'])
const router = useRouter()

var storeId = router.currentRoute.value.query.storeId

const weekHours = reactive<TimeRange>({
  openingTime: "",
  closingTime: ""
});

const weekendHours = reactive<TimeRange>({
  openingTime: "",
  closingTime: ""
});

const operatingHours = reactive<OperatingHours>({
  weekHours: weekHours,
  weekendHours: weekendHours
});

const aboutInfo = reactive<AboutInfo>({
  about: "",
  operatingHours: operatingHours
});

const storeInfo = reactive<StoreInfo>({
  storeName: "",
  googleMapsLink: "",
  aboutInfo: aboutInfo
});

const formData = reactive<RegisterDto>({
  name: "",
  username: "",
  role: "",
  address: "",
  phone: "",
  password: "",
  confirmPassword: "",
  storeInfo: storeInfo
});

// const formData = reactive<RegisterDto>({
//   name: "Nceba Dumasi",
//   username: "nceba.dumasi@gmail.com",
//   role: "owner",
//   address: "25 leeukop rd",
//   phone: "0614273890",
//   password: "qwertyuiop",
//   confirmPassword: "qwertyuiop",
//   // Owner information
//   storeName: "Master piece Barbershop - Haircut styling",
//   googleMapsLink: "https://maps.google.com/someting",
//   about: "At Masterpiece Barbershop, our dedicated team of skilled barbers are true artists in their craft, transforming your hair",
//   weekOperatingHours: "09:00 - 21:00",
//   weekendOperatingHours: "09:30 - 18:00"
// });

const showPassword = ref(false);
const showConfirmPassword = ref(false);

var loginErrorMsg = ref("")
var errorBool = ref(false)

const roleOptions = [
  { value: "customer", label: "Customer" },
  { value: "barber", label: "Barber" },
  { value: "owner", label: "Shop Owner" },
];

const togglePassword = () => {
  showPassword.value = !showPassword.value;
};

const toggleConfirmPassword = () => {
  showConfirmPassword.value = !showConfirmPassword.value;
};

const handleSubmit = () => {
  if (formData.password !== formData.confirmPassword) {
    alert("Passwords do not match");
    return;
  }
  console.log("Form submitted:", formData);
  register(formData).then(
    (value) => {
      
      if (storeId == null || storeId == undefined){
        storeId = router.currentRoute.value.query.storeId
      }
      cookies.set("token",value.data.token)
      cookies.set("id",value.data.user.id)
      if (formData.role == "owner"){
        
        storeId = value.data.user.storeInfo.id;
        cookies.set("token",value.data.token)
        cookies.set("id",value.data.user.id)
        router.push({ name: 'owner-onboarding', query: { storeId: storeId } })
      } else if (formData.role == "barber") {
        router.push({ name: 'store-queue', query: { storeId: storeId } })
      }
      else {
        cookies.set("token",value.data.token)
        cookies.set("id",value.data.user.id)
        router.push({ name: 'barber-home', query: { storeId: storeId } })
      }
    },
    (reason) => {
      errorBool.value = true
      loginErrorMsg.value = "User already registered"
    }
  
  )
};
</script>
