<template>
  <div class="flex flex-col mt-6 max-w-full w-[339px]">
    <h3 class="text-base font-bold text-gray-900 text-ellipsis">
      Payment summary
    </h3>
    <div class="flex flex-col items-center mt-3 w-full max-w-[339px]">
      <PaymentItem
        v-for="item in selectedServices.services"
        :key="item.name"
        :label="item.name"
        :amount="item.price"
      />
      <div class="flex gap-1 justify-center items-start mt-5">
        <div
          v-for="index in 21"
          :key="index"
          class="flex shrink-0 w-3 h-px rounded-md bg-neutral-300"
        ></div>
      </div>
      <div
        class="flex gap-10 justify-between items-start mt-5 w-full text-gray-900 min-h-[30px]"
      >
        <h4 class="text-base font-medium">Total price</h4>
        <span class="text-lg font-bold">{{ totalPrice }}</span>
      </div>
    </div>
  </div>
</template>

<script setup >
import { computed } from "vue";
import PaymentItem from "./PaymentItem.vue";
import { storeToRefs } from "pinia";
import { useBookingStore } from '../ownerServices/stores/serviceStore';

const store = useBookingStore();
const { selectedServices } = storeToRefs(store);
console.log(selectedServices.value);


    const totalPrice = computed(() => {
      const total = selectedServices.value.reduce((sum, item) => {        
        const amount = parseFloat(item.price);
        return sum + (isNaN(amount) ? 0 : amount);
      }, 0);
      return `R${total.toFixed(2)}`;
    });

  
</script>
