<template>
  <section
    aria-label="Service Input Section"
    class="flex flex-col justify-evenly mt-14 w-full min-h-[200px]"
  >
    <div class="flex w-full justify-evenly">
      <div class="flex flex-col self-start min-h-[91px]">
        <label for="priceInput" class="text-sm leading-none"
          >Price</label
        >
        <div class="flex flex-col mt-2 w-full text-xs">
          <div
            class="w-full border border-solid border-zinc-200 "
          ></div>
          <div class="flex gap-4 items-start py-2 w-full">
            <div
              class="flex items-center text-gray-900  w-[73px]"
            >
              <input
                type="number"
                id="priceInput"
                v-model="price"
                class="text-center rounded-lg border border-gray-300 border-solid w-full h-[34px]"
                aria-label="Price in Rands"
                step="0.01"
                min="0"
              />
            </div>
          </div>
        </div>
      </div>

      <div class="flex flex-col">
        <label
          for="serviceNameInput"
          class="text-sm leading-none text-neutral-800"
          >Service Name</label
        >
        <div class="flex flex-col mt-2 w-full">
          <div
            class="w-full border border-solid border-zinc-200 min-h-[1px]"
          ></div>
          <div class="flex gap-4 items-start py-2 w-full">
            <div
              class="flex w-[143px]"
            >
              <input
                type="text"
                id="serviceNameInput"
                v-model="serviceName"
                class="text-center rounded-lg border border-gray-300 border-solid w-full h-[34px]"
                aria-label="Service name"
              />
            </div>
            <button
              type="button"
              @click="addService"
              class="w-6 min-h-[39px]"
              aria-label="Add service"
            >
              <img
                loading="lazy"
                src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/7995ca140bb1233abfc973a8a131e3a5049e387171e15893efcea7d449078278?apiKey=a8777e7eadb646868aa33e965a4c2028&"
                alt="Add service icon"
                class="object-contain flex-1 shrink self-stretch my-auto w-5 rounded-none aspect-square basis-0"
              />
            </button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref } from "vue";
import { useServiceStore } from "../stores/serviceStore";

const store = useServiceStore();
const price = ref("");
const serviceName = ref("");

const addService = () => {
  if (price.value && serviceName.value) {
    store.addService({
      price: parseFloat(price.value),
      name: serviceName.value,
    });
    price.value = "";
    serviceName.value = "";
  }
};

</script>
