<template>
  <button
    type="submit"
    @click="handleSubmit"
    class="fixed bottom-0 start-0 py-5 mx-auto font-bold text-center text-white rounded-t-2xl bg-slate-700 w-full"
  >
    Finish Registration
  </button>
</template>

<script setup>
import { useCookies } from '@vueuse/integrations/useCookies'
import { useServiceStore } from "../stores/serviceStore";
import { useRouter } from 'vue-router'

const cookies = useCookies(['locale'])
const router = useRouter()

const storeId = router.currentRoute.value.query.storeId;

const store = useServiceStore();

const handleSubmit = () => {
  store.submitServices(storeId);
  // cookies.set("id",response.data.user.id)
  router.push({ name: 'store-queue', query: { storeId: storeId } })

};
</script>
