import { defineStore } from "pinia";
import { ref } from "vue";
import { addServicesToStore } from "../../api/ApiUtil"
import { useCookies } from '@vueuse/integrations/useCookies'
import { InMemService } from '../../interfaces/inMemStore/ServicesInterface'


export const useServiceStore = defineStore("service", () => {
  const services = ref<InMemService[]>([]);
  const editingIndex = ref(-1);

  const addService = (service: InMemService) => {
    services.value.push(service);
  };

  const deleteService = (index: number) => {
    services.value.splice(index, 1);
  };

  const setEditingService = (index: number) => {
    editingIndex.value = index;
  };

  const updateService = (index: number, service: InMemService) => {
    services.value[index] = service;
  };

  const submitServices = async (storeId: string) => {
    const cookies = useCookies()
    addServicesToStore(storeId ,cookies.get("token"),services.value)
  };

  return {
    services,
    editingIndex,
    addService,
    deleteService,
    setEditingService,
    updateService,
    submitServices,
  };
});

