<template>
  <button
    class="flex self-center fixed bottom-0 z-50 overflow-hidden gap-2.5 justify-center items-center px-2.5 py-4 max-w-[339px] w-full text-base font-bold text-center text-white rounded-lg bg-slate-700"
    @click="bookNow"
    aria-label="Book Now"
  >
    <span class="self-stretch my-auto">Book Now</span>
    <img
      loading="lazy"
      src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/88441c868800590ca78210e0147f1d81024b98c67d4427554cbdbf749b148ab6?apiKey=a8777e7eadb646868aa33e965a4c2028&"
      class="object-contain shrink-0 self-stretch my-auto aspect-square w-[18px]"
      alt=""
    />
  </button>
</template>

<script setup>
import { useRouter } from 'vue-router';
import { addCustomerToQueue } from "../api/ApiUtil";
import { useBookingStore } from '../ownerServices/stores/serviceStore';
import { useCookies } from '@vueuse/integrations/useCookies'
import { storeToRefs } from 'pinia';


const store = useBookingStore();
const paymentItems = storeToRefs(store);
const router = useRouter()
const storeId = router.currentRoute.value.query.storeId
const cookies = useCookies(['locale'])


const bookNow = () => {
      // Implement booking logic here
      console.log("Booking initiated");
      console.log(paymentItems);
      var servicesDto = []
      paymentItems.selectedServices.value.forEach((item) => {
        servicesDto.push(item.id)
      })
      addCustomerToQueue(storeId,cookies.get("token"),cookies.get("id"),servicesDto)
      .then(
        (value) => {
          router.push({
          name : "customer-booking-confirmation",
          query: { storeId: storeId }
      })
        },
        (reason) => {}
      )
      
    };
</script>
