<template>
  <section
    class="flex flex-col justify-center items-center mt-0 w-full min-h-[523px]"
    role="main"
  >
    <p
      class="flex w-full text-base font-bold text-center justify-center text-gray-900 h-4 text-ellipsis"
    >
      {{ description }}
    </p>

    <div class="flex flex-col px-5 mt-6 w-full">
      <img :src="qrcode" alt="QR Code" />
      <button
      class="overflow-hidden gap-2.5 self-stretch px-2.5 py-4 mt-6 max-w-full text-base font-bold text-center text-white whitespace-nowrap rounded-lg bg-slate-700 w-[339px] hover:bg-slate-800 focus:outline-none focus:ring-2 focus:ring-slate-500 focus:ring-offset-2"
      :aria-label="buttonAriaLabel"
      @click="handleContinue"
      tabindex="0"
    >
      {{ buttonText }}
    </button>
    </div>

    
  </section>
</template>

<script setup>
import { useQRCode } from '@vueuse/integrations/useQRCode'
import { backend_config, frontend_config } from '../../../config/config'
import { ref } from 'vue'
import { useRouter } from 'vue-router'
const router = useRouter()

const text = ref(`${frontend_config.host}?storeId=${router.currentRoute.value.query.storeId}`)

const qrcode = useQRCode(text)
const props = defineProps({
  description: {
    type: String,
    required: true,
  },
  buttonText: {
    type: String,
    required: true,
  },
  buttonAriaLabel: {
    type: String,
    required: true,
  },
});

const emit = defineEmits(["continue"]);

const handleContinue = () => {
  emit("continue");
};
</script>
