<template>
  <main class="flex flex-col py-2 w-full mt-32 mb-20" role="main">
    <AppHeader title="Services" />
    <section class="flex flex-col px-2" aria-labelledby="service-selection-title">
      <div class="flex flex-col w-full ">
        <h1
          id="service-selection-title"
          class="text-base font-bold text-gray-900"
        >
          Choose Service
        </h1>
        <div
          class="flex overflow-hidden overflow-x-auto mt-3 w-full"
          role="list"
        >
          <ServiceCard
            v-for="service in servicesFromApi"
            :key="service.id"
            :service="service"
            @select="selectService"
            />
        </div>
      </div>

      <section
        class="flex flex-col mt-3 max-w-full p-3"
        aria-labelledby="payment-summary-title"
      >
        <h2
          id="payment-summary-title"
          class="text-base font-bold text-gray-900"
        >
          Payment summary
        </h2>
        <div class="flex flex-col items-center mt-3 w-full">
          <PaymentSummaryItem
            v-for="item in selectedServices"
            :key="item.id"
            :item="item"
          />

          <DividerLine />

          <div
            class="flex gap-10 justify-between items-start mt-5 w-full text-gray-900 min-h-[30px]"
          >
            <span class="text-base font-medium">Service fee</span>
            <span class="text-lg font-bold">{{ serviceFee }}</span>
          </div>
        </div>
      </section>
    </section>

    <button
      class="flex w-full overflow-hidden gap-2.5 justify-center items-center px-2.5 py-4 max-w-[339px] self-center text-base font-bold text-center text-white rounded-lg bg-slate-700 hover:bg-slate-800 focus:ring-2 focus:ring-offset-2 focus:ring-slate-500 fixed bottom-0 z-50"
      @click="continueBooking"
      :disabled="!selectedServices.length"
      aria-label="Continue to booking"
    >
      <span class="self-stretch my-auto">Continue Booking</span>
      <img
        loading="lazy"
        src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/6df104b3a9ddbd530f9a04ec09023b8e70ebcb65dbf8c74bc358e9888ad8f521?apiKey=a8777e7eadb646868aa33e965a4c2028&"
        class="object-contain shrink-0 self-stretch my-auto rounded-md aspect-square w-[18px]"
        alt=""
      />
    </button>
  </main>
</template>

<script setup>
import AppHeader from "../queue/CustomerQueue/AppHeader.vue";

import { ref, computed } from "vue";
import ServiceCard from "./components/ServiceCard.vue";
import PaymentSummaryItem from "./components/PaymentSummaryItem.vue";
import DividerLine from "./components/DividerLine.vue";
import { useRouter } from 'vue-router';
import { getBarberDetails } from "../api/ApiUtil";
import { useCookies } from '@vueuse/integrations/useCookies'
import { useBookingStore } from '../ownerServices/stores/serviceStore';
import { storeToRefs } from "pinia";


const store = useBookingStore();
const { servicesFromApi, selectedServices, services } = storeToRefs(store);

const cookies = useCookies(['locale'])
const router = useRouter()
const storeId = router.currentRoute.value.query.storeId


getBarberDetails(storeId, cookies.get("token")).then(
  (value) => {
    if(value.data != "" && servicesFromApi.value.length < 1){
      value.data.storeServices.forEach(element => {
        element.imageAlt = "https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/0e2116e530047874382fee90b67eb7c5dab14b771b772fe5a2fae327ad95a92a?apiKey=a8777e7eadb646868aa33e965a4c2028&"
        element.imageUrl = "https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/0e2116e530047874382fee90b67eb7c5dab14b771b772fe5a2fae327ad95a92a?apiKey=a8777e7eadb646868aa33e965a4c2028&"
          servicesFromApi.value.push(element)        
      });      
    }

  },
  (reason) => {}
)


const serviceFee = computed(() => {
      const total = selectedServices.value.reduce((sum, item) => {        
        const amount = parseFloat(item.price);
        return sum + (isNaN(amount) ? 0 : amount);
      }, 0);
      return `R${total.toFixed(2)}`;
    });

const totalPrice = computed(() => {
      const total = selectedServices.value.reduce((sum, item) => {        
        const amount = parseFloat(item.price);
        return sum + (isNaN(amount) ? 0 : amount);
      }, 0);
      return `R${total.toFixed(2)}`;
    });

const selectService = (service) => {
  const index = selectedServices.value.findIndex((s) => s.id === service.id);
  if (index === -1) {
    selectedServices.value.push(service);
  } else {
    selectedServices.value.splice(index, 1);
  }
};

const continueBooking = () => {
  if (selectedServices.value.length) {
    router.push({ name: 'customer-booking-details', query: { storeId: storeId } })
  }
};
</script>
