<template>
  <section
    class="flex gap-3 items-start self-center p-2 mt-4 w-full text-sm text-white rounded-lg bg-opacity-60 max-w-[339px] shadow-[0px_2px_6px_rgba(28,39,49,0.08)]"
  >
    <img
      loading="lazy"
      src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/d73adad647c8a54689eeec777eceff40099984c4fe94da92e110e448535060c1?apiKey=a8777e7eadb646868aa33e965a4c2028&"
      class="object-contain shrink-0 rounded-lg aspect-square w-[78px]"
      alt="Barbershop service"
    />
    <div class="flex flex-col flex-1 shrink items-start basis-0">
      <h2 class="self-stretch text-base font-bold text-ellipsis">
        {{ shopData.name }}
      </h2>
      <div class="flex gap-2 items-center mt-2">
        <img
          loading="lazy"
          src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/afa94dab98317e9f6591867bf976631528cecb91fbaa0970664f7c7b165cb3ad?apiKey=a8777e7eadb646868aa33e965a4c2028&"
          class="object-contain shrink-0 self-stretch my-auto w-4 aspect-square"
          alt="Location icon"
        />
        <p class="self-stretch my-auto">{{ shopData.location }}</p>
      </div>
      <div class="flex gap-2 items-start mt-2 whitespace-nowrap">
        <img
          loading="lazy"
          src="https://cdn.builder.io/api/v1/image/assets/a8777e7eadb646868aa33e965a4c2028/60b39513b8c99f78744d017947022cecf70f561ffe5028c8de26bdd051b4ad5c?apiKey=a8777e7eadb646868aa33e965a4c2028&"
          class="object-contain shrink-0 w-4 rounded-md aspect-square"
          alt="Rating icon"
        />
        <span>{{ shopData.rating }}</span>
        <span>({{ shopData.reviewCount }})</span>
      </div>
    </div>
  </section>
</template>

<script setup>
import { getBarberDetails } from "../api/ApiUtil";
import { useRouter } from 'vue-router'
import { useCookies } from '@vueuse/integrations/useCookies'
import { ref } from "vue";

const cookies = useCookies(['locale'])

const router = useRouter()

const storeId = router.currentRoute.value.query.storeId

const shopData = ref({
  name: "",
  mainImage: "",
  isOpen: false,
  location: "No location set...",
  rating: 0.0,
  reviewCount: 0,
  about: "No about Set...",
  googleMapsLink: "https://maps.google.com",
  storeServices: []
});
const isStoreValid = ref(false);

getBarberDetails(storeId, cookies.get("token")).then(
  (value) => {
    if(value.data != ""){
      isStoreValid.value = true

      shopData.value.name = value.data.storeName
      shopData.value.mainImage = "https://plus.unsplash.com/premium_photo-1661380558859-40df8dd91dfd?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D"
      shopData.value.isOpen = true
      shopData.value.location = "25 Leeukop Rd, Woodmead, Sandton" // value.data.location
      shopData.value.rating = 0.0
      shopData.value.reviewCount = 0
      shopData.value.about = value.data.aboutInfo.about
      shopData.value.googleMapsLink = "https://www.google.com/maps/place/25+Leeuwkop+Rd,+Woodmead,+Sandton,+2191"
      shopData.value.storeServices = value.data.storeServices

    }

  },
  (reason) => {}
)

</script>
