export function login(loginDto : LoginDto) {
    return axios.post(`${host}/api/auth/login`, loginDto)
}

export function getBarberDetails(storeId : String |LocationQueryValue | LocationQueryValue[], token: String) {
    return axios.get(`${host}/api/stores?storeId=${storeId}`, { headers: {"Authorization" : `Bearer ${token}`} })  
}

export function addServicesToStore(storeId: String, token: String, servicesDto: InMemService[] | undefined) {
    return axios.post(`${host}/api/stores/services/add?storeId=${storeId}`,servicesDto, { headers: {"Authorization" : `Bearer ${token}`} })  
}

export function addCustomerToQueue(storeId: String, token: String, customerId: String, bookingDto: Array<string>) {
    return axios.post(`${host}/api/stores/queue/add?storeId=${storeId}&customerId=${customerId}`,bookingDto, { headers: {"Authorization" : `Bearer ${token}`} })  
}

export function acceptCustomerFromQueue(storeId: String, token: String, customerId: String) {
    return axios.get(`${host}/api/stores/queue/accept?storeId=${storeId}&customerId=${customerId}`, { headers: {"Authorization" : `Bearer ${token}`} })  
}

export function completeCustomerFromQueue(storeId: String, token: String, customerId: String) {
    return axios.get(`${host}/api/stores/queue/accept?storeId=${storeId}&customerId=${customerId}`, { headers: {"Authorization" : `Bearer ${token}`} })  
}

export function getCustomerByList(token: String, customerListDto: Array<string>) {
    console.log(customerListDto);
    return axios.post(`${host}/api/user/list/all`,customerListDto, { 
        headers: {"Authorization" : `Bearer ${token}`}} )
}