<template>
  <ul class="mt-6">
    <li
      v-if="activeQueueTab == 'In Queue'"
      v-for="queueEntity in queueInQueue"
      :key="queueEntity.id"
      class="flex gap-10 justify-between items-center py-3 w-full rounded-lg"
    >
      <div
        class="flex gap-2 items-center self-stretch my-auto text-base font-bold text-gray-900"
      >
        <!-- <img
          :src="customer.avatar"
          :alt="customer.name"
          class="object-contain shrink-0 self-stretch my-auto w-14 rounded-full aspect-square"
          loading="lazy"
        /> -->
        <span class="self-stretch my-auto">{{ resolveCustomer(queueEntity.customerId).name }}</span>
      </div>
      <button
        @click="acceptCustomer(queueEntity.customerId)"
        class="overflow-hidden gap-2.5 self-stretch px-4 py-2 my-auto text-sm font-medium text-center whitespace-nowrap rounded-lg border-solid border-[1.5px] border-slate-700 text-slate-700"
      >
        Accept
      </button>
    </li>

    <li
      v-if="activeQueueTab == 'Ongoing'"
      v-for="queueEntity in queueOngoing"
      :key="queueEntity.id"
      class="flex gap-10 justify-between items-center py-3 w-full rounded-lg"
    >
      <div
        class="flex gap-2 items-center self-stretch my-auto text-base font-bold text-gray-900"
      >
        <!-- <img
          :src="customer.avatar"
          :alt="customer.name"
          class="object-contain shrink-0 self-stretch my-auto w-14 rounded-full aspect-square"
          loading="lazy"
        /> -->
        
        <span class="self-stretch my-auto">{{ resolveCustomer(queueEntity.customerId).name }}</span>
      </div>
      <button
        @click="completeCustomer(queueEntity.customerId)"
        class="overflow-hidden gap-2.5 self-stretch px-4 py-2 my-auto text-sm font-medium text-center whitespace-nowrap rounded-lg border-solid border-[1.5px] border-slate-700 text-slate-700"
      >
        Complete
      </button>
      
    </li>
  </ul>
</template>

<script setup>
import { ref } from "vue";
import { acceptCustomerFromQueue, getBarberDetails, getCustomerByList, completeCustomerFromQueue } from "../../api/ApiUtil";
import { useRouter } from 'vue-router'
import { useCookies } from '@vueuse/integrations/useCookies'

const props = defineProps({
  activeQueueTab: {
    type: String,
    required: true,
  },
})

const cookies = useCookies()
const router = useRouter()
const storeId = router.currentRoute.value.query.storeId
var customers = ref([]);

var queue = ref([])
var queueOngoing = ref([])
var queueInQueue = ref([])
var queueComplete = ref([])

// customers = customers.value.filter(item => item.)

getBarberDetails(storeId, cookies.get("token")).then(
  (value) => {
    if(value.data != ""){
      queue = ref(value.data.storeQueue.storeQueueEntries)
      queueOngoing.value = Array.isArray(queue.value) ? queue.value.filter(item => item.accepted && item.dateComplete == null) : [];
      queueInQueue.value = Array.isArray(queue.value) ? queue.value.filter(item => !item.accepted && item.dateComplete == null) : [];
      queueComplete.value = Array.isArray(queue.value) ? queue.value.filter(item => item.dateComplete != null) : [];      
      
      getCustomerByList(cookies.get("token"), queue.value.map(item => item.customerId)).then(
        (value) => {
          customers.value = value.data
        },
        (error) => {}
      )
    }

  },
  (reason) => {}
)


var resolveCustomer = (uuid) => {  
  return customers.value.find(customer => customer.id === uuid) || {};
};

const acceptCustomer = (id) => {
  acceptCustomerFromQueue(storeId, cookies.get("token"), id).then(
    (value) => {
      console.log(value)
    },
    (reason) => {
      console.log(reason);
    }
  )
  location.reload()
};

const completeCustomer = (data) =>{
  // console.log(data);
  
  completeCustomerFromQueue(storeId, cookies.get("token"), data).then(
    (value) => {
      console.log(value)
    },
    (reason) => {
      console.log(reason);
    }
  )
  location.reload()
}
</script>
